    width: 50%;
    margin: auto;
    text-align: left;
    @media (max-width: 767px) {
      width: 90%;
    }
  }
  .submit-button {
    transition: .25s all ease;
    background: #0069eb;
    border-radius: 30px;
    border: none;
    color: #fff;
    font-size: 16px;
    padding: 15px 30px;
    text-decoration: none;
    cursor: pointer;
    &:hover {
      opacity: 0.8;
    }
    &:disabled {
      background: #bbbbbb;
      cursor: not-allowed;
    }
