<template>
  <div id="app">
    <nav>
      <div class="logo-container">
        <img :src="Logo">
      </div>
    </nav>
    <router-view/>
    <div id="footer">
      <p>Copyright © Gridow Inc.</p>
    </div>
  </div>
</template>

<script>
import Logo from '@/assets/gridow_logo.png';
export default {
  name: 'App',
  data() {
    return {
      Logo
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
}
nav {
  border-bottom: 4px solid #085ba1;
}
.logo-container {
  width: 100%;
  max-width: 1160px;
  margin: auto;
  text-align: left;
}
.logo-container img {
  width: 200px;
  padding: 10px 0;
}
@media (max-width: 767px) {
  nav img {
    max-width: 100%;
  }
}
#footer {
  width: 100%;
  /* height: 60px; */
  height: 40px;
  text-align: center;
  /* padding-top: 20px; */
  padding: 10px 0;
  color: #fff;
  font-style: normal;
  font-size: 12px;
  /* background-image: -webkit-gradient(linear, 91.95% 0.00%, 8.05% 100.00%, color-stop( 0% , rgba(0,59,214,1.00)),color-stop( 100% , rgba(44,137,58,1.00)));
  background-image: -webkit-linear-gradient(230deg,rgba(0,59,214,1.00) 0%,rgba(44,137,58,1.00) 100%);
  background-image: linear-gradient(220deg,rgba(0,59,214,1.00) 0%,rgba(44,137,58,1.00) 100%); */
  background-color: #085ba1;
}
</style>



